import React from 'react';
import Loader from '../components/Loader';
import styles from '../styles/IngredientDetails.module.css'
import { useParams } from 'react-router-dom';
import { useAppSelector } from '../Hooks/Hooks';
import { FC } from "react";
import { TIngredient } from '../services/redusers';

interface IIngredient {
  constructor: any,
  profile?: any,
  data: any
}

const Ingredient: FC<IIngredient> = ({constructor, profile}) => {

  let { _id } = useParams();
  const { data } = useAppSelector((state) => state.mainReducer);

  if (data.length < 1) {
    return <Loader />;
  } else {
    return (
      <>
        {data
          .filter((item: TIngredient) => item._id === _id)
          .map((item: TIngredient, index: number) => {
            return (
              <section key={index}>
                <div className={styles.ingredientDetails + ' pt-10 pl-10'}>
                  <h2 className="text text_type_main-large">
                     
                  </h2>
                </div>
                <div className={styles.modalContainer}>
                  <img
                    src={item.image}
                    alt=""
                    className={styles.ingredientImage + ' mb-4'}
                  />
                  <p className="text text_type_main-medium mb-8">{item.name}</p>
                  <div className={styles.ingredientContainer + ' mb-15'}>
                    <div className={styles.modalContainer}>
                      <p className="text text_type_main-default text_color_inactive">
                        ,
                      </p>
                      <p className="text text_type_digits-default text_color_inactive">
                        {item.calories}
                      </p>
                    </div>
                    <div className={styles.modalContainer}>
                      <p className="text text_type_main-default text_color_inactive">
                        , 
                      </p>
                      <p className="text text_type_digits-default text_color_inactive">
                        {item.proteins}
                      </p>
                    </div>
                    <div className={styles.modalContainer}>
                      <p className="text text_type_main-default text_color_inactive">
                        , 
                      </p>
                      <p className="text text_type_digits-default text_color_inactive">
                        {item.fat}
                      </p>
                    </div>
                    <div className={styles.modalContainer}>
                      <p className="text text_type_main-default text_color_inactive">
                        , 
                      </p>
                      <p className="text text_type_digits-default text_color_inactive">
                        {item.carbohydrates}
                      </p>
                    </div>
                  </div>
                </div>
              </section>
            );
          })}
      </>
    );
  }
};

export default Ingredient;